import React from 'react'
import 'bootstrap/dist/css/bootstrap.min.css';
function Spinner() {
    return (
        <div class="d-flex align-items-center">
            <h2>Loading...</h2>
            <div class="spinner-border ml-auto" role="status" aria-hidden="true"></div>
        </div>
    )
}

export default Spinner
